import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import Metadata from "../components/metadata"

import thoughtsOnProgramming from "../thoughts-on-programming.json"
import "./thoughts-on-programming.scss"

const meta = {
  title: "Thoughts On Programming",
  description: "",
}

const Thought = it => (
  <div className="thought" key={it.id}>
    <a href={it.url} target="_blank" rel="noreferrer">
      <img alt={it.description} width={293} height={293} src={it.url} />
    </a>
  </div>
)

const ThoughtsOnProgrammingPage = ({ data }) => {
  const files = data.allFile.edges.map(it => it.node)
  const thoughts = thoughtsOnProgramming
    .map(thought => {
      const url = files.find(it => it.name === thought.id).publicURL

      return {
        ...thought,
        url,
      }
    })
    .sort((a, b) => b.date - a.date)

  return (
    <Layout>
      <Metadata title={meta.title} description={meta.description} />
      <section>
        <div className="row">
          <div className="col-xs-12 col-sm-12 col-md-9 col-lg-9">
            <h1 className="title">{meta.title}</h1>
            <p>We have compiled a list of quotes which help you start the day with positive vibes! You can follow us on <a href="https://www.instagram.com/jugendstil_io/" rel="noreferrer" target="_blank">Instagram</a>, <a href="https://twitter.com/jugendstil_io/" rel="noreferrer" target="_blank">Twitter</a> or <a href="https://www.linkedin.com/company/jugendstil-io/" rel="noreferrer" target="_blank">LinkedIn</a> to get the latest #ThoughtsOnProgramming straight to your inbox.</p>
          </div>
        </div>
        <div className="row">{thoughts.map(Thought)}</div>
      </section>
    </Layout>
  )
}

export default ThoughtsOnProgrammingPage

export const pageQuery = graphql`
  {
    allFile(filter: { relativePath: { regex: "/thoughts-on-programming/" } }) {
      edges {
        node {
          ext
          name
          publicURL
          relativePath
        }
      }
    }
  }
`
